<template>
  <div>
    <el-row :gutter="10" class="app-panel-container">
      <el-col
        :sm="10"
        :md="8"
        :lg="6"
        v-for="item in data"
        v-bind:key="item.websiteId"
        class="app-panel-item"
      >
        <a :href="item.websiteUrl" target="_blank">
          <el-card :body-style="{ padding: '0px' }" class="appList">
            <i class="el-icon-edit" @click.stop="clickSite($event, item)"></i>
            <!-- <img class="web-icon" :src="item.icon" /> -->
            <el-avatar
              class="web-icon"
              :size="50"
              :src="item.websiteIcon ? item.websiteIcon : item.websiteUrl + '/favicon.ico'"
              shape="square"
              fit="fill"
            >SITE</el-avatar>
            <span>{{ item.websiteName }}</span>
          </el-card>
        </a>
      </el-col>
    </el-row>

    <el-dialog title="增加自定义网站" :visible.sync="dialogFormVisible">
      <el-form :model="webForm">
        <el-form-item label="网站名称" label-width="120px">
          <el-input v-model="webForm.websiteName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="网站url">
          <el-input v-model="webForm.websiteUrl" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="网站icon">
          <el-input v-model="webForm.websiteIcon" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="delSite">删除网站</el-button>
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editSite">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import userWebApi from '@/api/wei-index/userSiteApi'
export default {
  props: {
    data: Array
  },
  data() {
    return {
      webForm: {},
      dialogFormVisible: false
    }
  },
  created() {
    console.log(this.data)
  },
  methods: {
    /**
     * 修改app数据
     */
    clickSite(e, item) {
      // 阻止a标签默认行为
      e.preventDefault()
      this.dialogFormVisible = true
      this.webForm = item
      // this.$notify({
      //   title: 'It works!',
      //   type: 'success',
      //   message:
      //     "We've laid the ground work for you. It's time for you to build something epic!",
      //   duration: 5000
      // })
    },
    editSite(e) {
      console.log(this.webForm)
      userWebApi
        .updateSite(this.webForm)
        .then(result => {
          this.$router.go(0)
        })
    },
    delSite(e) {
      console.log(this.webForm)
      userWebApi
        .delSite(this.webForm.websiteId)
        .then(result => {
          this.$router.go(0)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style lang="scss">
@mixin setBackground($value) {
  background-color: $value;
  color: white;
}

.app-panel-container {
  padding: 10px;
  background: #f6f6f6;

  .app-panel-item {
    margin-bottom: 20px;

    & > a {
      font-size: 14px;
      text-decoration: none;
      text-align: center;

      .el-card {
        margin: 0 auto;
        position: relative;
        .el-icon-edit {
          display: none;
          padding: 5px;
          font-size: 15px;
          right: 0;
          top: 0;
          color: #888;
          background: #fff;
          position: absolute;
        }
        .web-icon {
          border-radius: 5px;
        }
      }
      .el-card:hover .el-icon-edit {
        display: block;
      }

      .appList:hover {
        @include setBackground(#00e6ff);
      }
      .appList {
        background: #fff;
        width: 120px;
        padding: 5px 20px;
        box-sizing: border-box;
        border-radius: 5px;
        // img {
        //   height: 60px;
        //   padding: 0 5px;
        // }
        span {
          display: inline-block;
          white-space: nowrap;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
</style>



