<template>
  <div id="body">
    <el-container>
      <!-- 顶栏容器 -->
      <el-header ref="head">
        <div class="header-container">
          <a href>
            <img
              src="@/assets/img/weindex.png"
              class="header-logo"
              alt="banner"
            />
          </a>
          <div style="flex: 1"></div>
          <router-link to="/login">
            <el-avatar
              style="margin: 0 5px; line-height: 50px"
              :size="50"
              :src="isLogin ? avatar : ''"
              >登录</el-avatar
            >
          </router-link>
          <el-button
            @click="drawer = true"
            type="primary"
            icon="el-icon-setting"
            >所有网站</el-button
          >
        </div>
      </el-header>
      <!-- 侧边栏容器 -->
      <el-aside ref="side">
        <el-drawer title="我是标题" :visible.sync="drawer">
          <DrawerBlock></DrawerBlock>
        </el-drawer>
      </el-aside>
      <!-- 主要区域容器 -->
      <el-main ref="main" v-bind:style="{ minHeight: height + 'px' }">
        <el-row>
          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <Banner />
          </el-col>
          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <Search
              class="search-container"
              :searchEngine="indexSiteData.engineSite"
            />
          </el-col>

          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <WebApp :siteData="indexSiteData" />
          </el-col>
        </el-row>
      </el-main>
      <!-- 底栏容器 -->
      <el-footer height="35px" ref="footer">
        <p @click="$router.push({ name: 'aboutus' })" target="_blank">
          微意首页 · 关于我们
        </p>
        <!-- <a href="https://beian.miit.gov.cn/" target="_blank">Copyright © 2021 微意首页 粤ICP备XXXXX号</a> -->
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import Search from '@/components/index/Search.vue'
import Banner from '@/components/index/Banner.vue'
import WebApp from '@/components/index/WebApp.vue'
import DrawerBlock from '@/components/index/DrawerBlock.vue' // 右侧边栏
import websiteApi from '@/api/wei-index/websiteApi'
import userSiteApi from '@/api/wei-index/userSiteApi'

export default {
  name: 'WeIndex',
  data() {
    return {
      height: 0,
      search: '',
      select: '',
      scrollElement: '',
      drawer: false,
      isLogin: false,
      avatar: '',
      indexSiteData: {}
    }
  },
  created() {
    this.getDefaultSite()
  },
  components: {
    Search,
    Banner,
    WebApp,
    DrawerBlock
  },
  methods: {
    // 获取首页网站
    getDefaultSite() {
      // 如果没有token或者登陆过期才获取默认首页网站
      let expTime = this.$storage.get('expireTime')
      if (new Date(expTime) - new Date() <= 0) {
        console.log('未登陆或登陆过期，获取默认站点')
        websiteApi.defaultSite().then(res => {
          this.indexSiteData = res.data
        })
      } else {
        console.log('已登录，获取用户站点')
        userSiteApi.userSite().then(res => {
          this.indexSiteData = res.data || {}
        })
        this.isLogin = true
        this.avatar = this.$storage.get('avatar')
      }
    },
    // 重置窗口可视高度
    resetDocumentClientHeight() {
      this.documentClientHeight = document.documentElement['clientHeight']
      window.onresize = () => {
        this.documentClientHeight = document.documentElement['clientHeight']
      }
    },
    // 点击登录
    login() {}
  },
  mounted() {
    // console.log(encodeURI('1dasd'))
    // 动态设置内容高度 让footer始终居底   header+footer的高度是35
    this.height =
      document.documentElement.clientHeight -
      this.$refs.head.$el.clientHeight -
      this.$refs.side.$el.clientHeight -
      this.$refs.footer.$el.clientHeight // 监听浏览器窗口变化
    window.onresize = () => {
      this.height =
        document.documentElement.clientHeight -
        this.$refs.head.$el.clientHeight -
        this.$refs.side.$el.clientHeight -
        this.$refs.footer.$el.clientHeight -
        35
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss">
body {
  width: 100%;
}
.el-header,
.el-footer {
  color: #333;
  &.el-footer {
    font-size: 13px;
    text-align: center;
    & > a {
      text-decoration: none;
      color: #666;
    }
  }
}

.header-container {
  display: flex;
  box-sizing: border-box;
  padding: 2px 5px;
  background: #fff;
  img {
    height: 50px;
  }
  .el-button {
    margin: 0 0 0 15px;
  }
}

.search-container {
  padding-bottom: 15px;
}
</style>



