<template>
  <div>
    <el-tabs
      type="border-card"
      v-model="editableTabsValue"
      @tab-add="dialogFormVisible = true"
      addable
    >
      <el-tab-pane label="我的应用" key="我的应用">
        <Block :data="siteData.mySite"></Block>
      </el-tab-pane>
      <el-tab-pane label="微意应用" key="微意应用">
        <Block :data="siteData.weiSite"></Block>
      </el-tab-pane>
      <el-tab-pane label="充电中心" key="充电中心">
        <Block :data="siteData.studySite"></Block>
      </el-tab-pane>
    </el-tabs>
    <!-- Form -->
    <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button>

    <el-dialog title="增加自定义网站" :visible.sync="dialogFormVisible">
      <el-form :model="webForm">
        <el-form-item label="网站名称" label-width="120px">
          <el-input v-model="webForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="网站url">
          <el-input v-model="webForm.url" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="网站icon">
          <el-input v-model="webForm.icon" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSite">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Block from '@/components/index/Block.vue'
import userWebApi from '@/api/wei-index/userSiteApi'

export default {
  components: { Block },
  props: {
    siteData: Object
  },
  data() {
    return {
      engineSite: [],
      mySite: [],
      studySite: [],
      weiSite: [],
      editableTabsValue: 0,
      webForm: {},
      dialogFormVisible: false
    }
  },
  created() {
    // this.engineSite = this.siteData.engineSite
    // this.mySite = this.siteData.mySite
    // this.studySite = this.siteData.studySite
    // this.weiSite = this.siteData.weiSite
  },
  methods: {
    // 增加网站
    addSite() {
      console.log(this.webForm)
      console.log(this.editableTabsValue)
      this.dialogFormVisible = false
      userWebApi
        .addSite({
          userWebName: this.webForm.name,
          userWebIcon: this.webForm.icon,
          userWebUrl: this.webForm.url,
          WebGroupId: +this.editableTabsValue + 1
        })
        .then(result => {
          this.$router.go(0)
        })
    }
  }
}
</script>



