import request from '@/utils/request'

const userWebApi = {
  baseUri: '/user_web',
  userSite: function () {
    return request({
      url: this.baseUri,
      method: 'get'
    })
  },
  addSite: function (param) {
    return request({
      url: this.baseUri,
      method: 'post',
      data: {
        userWebName: param.userWebName,
        userWebIcon: param.userWebIcon,
        userWebUrl: param.userWebUrl,
        WebGroupId: param.WebGroupId,
        sort: 2
      }
    })
  },
  updateSite: function (param) {
    return request({
      url: this.baseUri,
      method: 'put',
      data: {
        userWebId: param.websiteId,
        userWebName: param.websiteName,
        userWebIcon: param.websiteIcon,
        userWebUrl: param.websiteUrl
      }
    })
  },
  delSite: function (parameter) {
    return request({
      url: this.baseUri + `/${parameter}`,
      method: 'delete'
    })
  }
}

export default userWebApi



